import { Suspense, lazy } from "react";
import { useLocation, useSearchParams } from "react-router-dom";
import UserMemberMainWrapper from "../../Layout/UserMemberTab/UserMemberMainWrapper";
import QvipleLoader from "../../Loader/QvipleLoader";
import LmsManagerProfile from "./LmsManagerProfile";
import Loading from "../../Loader/Loading";
import { useLmsDashboardMaster } from "../../Hooks/ApiHooks/LmsManager/lms-manager-api";
import UserMemberTabsWrapper from "../../Ui/UserMember/UserMemberTabsWrapper";

// import ExcelImport from "../../ExcelImport/ExcelImport";

// const Attendence = lazy(() =>
//   import("../../../StaffTab/Attendence/Attendence")
// );

// const LmsSettoffRule = lazy(() => import("./LmsSettoffRule/LmsSettoffRule"));
// const LmsHoliday = lazy(() => import("./LmsHoliday/LmsHoliday"));
// const LmsLeaveRequest = lazy(() => import("./LmsLeaveRequest/LmsLeaveRequest"));
// const LmsLeaveIssued = lazy(() => import("./LmsLeaveIssued/LmsLeaveIssued"));
const LmsAllStaff = lazy(() => import("./LmsAllStaff/LmsAllStaff"));
const LmsModerator = lazy(() => import("./LmsModerator/LmsModerator"));
const LmsAllottment = lazy(() => import("./LmsAllottment/LmsAllottment"));
const LmsLeaveManageCoff = lazy(() =>
  import("./LmsLeaveManageCoff/LmsLeaveManageCoff")
);
const LmsStaffOverview = lazy(() =>
  import("./LmsStaffOverview/LmsStaffOverview")
);

const LmsModeratorProfile = lazy(() => import("./LmsModeratorProfile"));
const LmsRecommendationAuthority = lazy(() =>
  import("./LmsRecommendationAuthority/LmsRecommendationAuthority")
);
const LmsReviewAuthority = lazy(() =>
  import("./LmsReviewAuthority/LmsReviewAuthority")
);
const LmsSanctionAuthority = lazy(() =>
  import("./LmsSanctionAuthority/LmsSanctionAuthority")
);
const LmsAllLeave = lazy(() => import("./LmsAllLeave/LmsAllLeave"));
const LmsBiometric = lazy(() => import("./LmsBiometric/LmsBiometric"));
const LmsAttendance = lazy(() => import("./LmsAttendance/LmsAttendance"));
const LmsLeaveRequestConfig = lazy(() =>
  import("./LmsLeaveRequest/LmsLeaveRequestConfig")
);
const LmsSettingConfig = lazy(() => import("./LmsSetting/LmsSettingConfig"));
const LmsManager = () => {
  const getQuery = useLocation();
  const [searchParams] = useSearchParams();
  const { lmsDashboardMaster, lmsDashboardMasterLoading } =
    useLmsDashboardMaster({
      data: {
        lmid: getQuery.state?.lmid,
        mid: "",
      },
      skip: !getQuery.state?.lmid,
    });

  // console.info("lmsDashboardMaster", lmsDashboardMaster);
  return (
    <>
      <UserMemberMainWrapper>
        {/* <ExcelImport onClose={() => {}} /> */}
        <Suspense fallback={<QvipleLoader />}>
          {!searchParams.get("type") ? (
            <>
              {getQuery.state?.role ? (
                <LmsModeratorProfile />
              ) : (
                <UserMemberTabsWrapper>
                  <LmsManagerProfile
                    tabManage={lmsDashboardMaster?.tab_manage}
                  />
                  {lmsDashboardMasterLoading && <Loading />}
                </UserMemberTabsWrapper>
              )}
            </>
          ) : (
            <>
              {getQuery.state?.role ? (
                <>
                  {getQuery.state?.role === "LEAVE_RECOMMENDATION_ACCESS" ? (
                    <LmsRecommendationAuthority />
                  ) : getQuery.state?.role === "LEAVE_REVIEW_ACCESS" ? (
                    <LmsReviewAuthority />
                  ) : getQuery.state?.role === "LEAVE_SANCTION_ACCESS" ? (
                    <LmsSanctionAuthority />
                  ) : null}
                </>
              ) : (
                <>
                  {searchParams.get("type") === "staff" ? (
                    <LmsAllStaff
                      instituteId={lmsDashboardMaster?.institute?._id}
                      staffAlias={
                        lmsDashboardMaster?.institute?.alias_pronounciation
                          ?.staff_alias
                      }
                    />
                  ) : searchParams.get("type") === "attendance" ? (
                    <LmsAttendance
                      instituteId={lmsDashboardMaster?.institute?._id}
                    />
                  ) : searchParams.get("type") === "moderator" ? (
                    <LmsModerator
                      lmid={getQuery.state?.lmid}
                      instituteId={lmsDashboardMaster?.institute?._id}
                    />
                  ) : searchParams.get("type") === "allottment" ? (
                    <LmsAllottment
                      lmid={getQuery.state?.lmid}
                      instituteId={lmsDashboardMaster?.institute?._id}
                    />
                  ) : //  searchParams.get("type") === "setoff" ? (
                  //   <LmsSettoffRule
                  //     lcid={lmsDashboardMaster?.leave_config?._id}
                  //     instituteId={lmsDashboardMaster?.institute?._id}
                  //     leaveTypeToggle={lmsDashboardMaster?.leave_manage}
                  //   />
                  // ) :
                  // searchParams.get("type") === "holiday" ? (
                  //   <LmsHoliday
                  //     lcid={lmsDashboardMaster?.leave_config?._id}
                  //     instituteId={lmsDashboardMaster?.institute?._id}
                  //   />
                  // ) :
                  searchParams.get("type") === "request" ? (
                    <LmsLeaveRequestConfig
                      lmid={getQuery.state?.lmid}
                      instituteId={lmsDashboardMaster?.institute?._id}
                    />
                  ) : // searchParams.get("type") === "request" ? (

                  //   <LmsLeaveRequest
                  //     lmid={getQuery.state?.lmid}
                  //     instituteId={lmsDashboardMaster?.institute?._id}
                  //   />
                  // ) : searchParams.get("type") === "issued" ? (
                  //   <LmsLeaveIssued
                  //     lmid={getQuery.state?.lmid}
                  //     instituteId={lmsDashboardMaster?.institute?._id}
                  //   />
                  // ) :
                  searchParams.get("type") === "coff" ? (
                    <LmsLeaveManageCoff
                      lmid={getQuery.state?.lmid}
                      instituteId={lmsDashboardMaster?.institute?._id}
                    />
                  ) : searchParams.get("type") === "leave" ? (
                    <LmsAllLeave
                      lmid={getQuery.state?.lmid}
                      lcid={lmsDashboardMaster?.leave_config?._id}
                      instituteId={lmsDashboardMaster?.institute?._id}
                      leaveTypeToggle={lmsDashboardMaster?.leave_manage}
                    />
                  ) : searchParams.get("type") === "biometric" ? (
                    <LmsBiometric
                      lmid={getQuery.state?.lmid}
                      instituteId={lmsDashboardMaster?.institute?._id}
                    />
                  ) : //  searchParams.get("type") === "attend" ? (
                  //   <LmsAttendance
                  //     lmid={getQuery.state?.lmid}
                  //     instituteId={lmsDashboardMaster?.institute?._id}
                  //   />
                  // ) :
                  searchParams.get("type") === "staff-overview" ? (
                    <LmsStaffOverview
                      leaveTypeToggle={lmsDashboardMaster?.leave_manage}
                    />
                  ) : searchParams.get("type") === "setting" ? (
                    <LmsSettingConfig
                      lmid={getQuery.state?.lmid}
                      leaveTypeToggle={lmsDashboardMaster?.leave_manage}
                      lcid={lmsDashboardMaster?.leave_config?._id}
                      instituteId={lmsDashboardMaster?.institute?._id}
                    />
                  ) : null}
                </>
              )}
            </>
          )}
        </Suspense>
      </UserMemberMainWrapper>
    </>
  );
};

export default LmsManager;
